body {
    background-color: var(--background-color);
    color: var(--primary-text-color);
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    letter-spacing: 0.04em;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    /* height: auto; */
}

button {
    cursor: pointer;
}

.list {
    list-style: none;
    padding-left: 0px;
    margin: 0px;
}

.link {
    text-decoration: none;
    color: inherit;
}