:root {
  --primary-text-color: #131313;
  --title-text-color: #000;
  --title-text-color-secondary: #fcfcfc;
  --background-color: #84a178;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--background-color);
  color: var(--primary-text-color);
  letter-spacing: .04em;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (max-width: 320px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.header {
  color: #adff2f;
}

